img {
    margin: -1vh 0 0 2vh;
}

#weather-head {
    font-family: 'Montserrat', sans-serif;
    font-size: 50px;
    letter-spacing: 0.1em;
    padding-top: 6vh;
    text-align: center;
    font-weight: 500;
    color: white;
}

.input-container {
    padding: 6vh 0;
    display: flex;
    justify-content: center;
}

.weather-data {
    color: white;
    font-family: 'Montserrat', sans-serif;
    padding: 2vh 0;
    text-align: center;
}

input {
    outline: none;
    border: none;
    padding: 0 7vh;
}

#cityName {
    font-family: 'Montserrat', sans-serif;
    padding-left: 1.5vh;
    font-weight: 500;
}

.search-btn {
    font-weight: 500;
    background-color: rgb(238, 73, 51);
    color: white;
    padding: 1.5vh 4vh;
    border: none;
    font-family: 'Montserrat', sans-serif;
    outline: none !important;
}

/* weather animation */

html, body, canvas {
    width: 100%;
    height: 100%;
}

body {
    background-color: #222;
    background-image: url(/Weatherapp/static/media/wallpaper2you_568737.351c9e62.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
}

body:after {
    z-index: -1;
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    animation: thunder-bg 1s infinite;
}

canvas {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
}

#canvas3 {
    z-index: -5;
}

#canvas2 {
    z-index: -10;
}

#canvas1 {
    z-index: -100;
}

@keyframes thunder-bg {
    0% {
        background-color: rgba(34, 34, 34, 0.9);
    }
    9% {
        background-color: rgba(34, 34, 34, 0.9);
    }
    10% {
        background-color: rgba(59, 59, 59, 0.3);
    }
    10.5% {
        background-color: rgba(34, 34, 34, 0.9);
    }
    80% {
        background-color: rgba(34, 34, 34, 0.9);
    }
    82% {
        background-color: rgba(59, 59, 59, 0.3);
    }
    83% {
        background-color: rgba(34, 34, 34, 0.9);
    }
    83.5% {
        background-color: rgba(59, 59, 59, 0.3);
    }
    100% {
        background-color: rgba(34, 34, 34, 0.9);
    }
}
